export function localeNumber(num, locale) {   
  if (locale == 'ro-RO') {
    return num.toLocaleString('en-US')
  }
  return num.toLocaleString(locale)
}

export function PromoCodeGenerator(num) {
    const dictionary =
      '1234567890qwertyuiopasdfghjklzxcvbnmQWERTYUIOPASDFGHKLZXCVBNM';
    let res = '';
    for (let index = 0; index < num; index++) {
      res += dictionary[Math.floor(Math.random() * (dictionary.length - 1))];
    }
    return res;
}

export function ConvertDateToString(date, locale) {
  return date.toLocaleString(locale, { timeZone: 'UTC' }).substr(0, 10);
}

export function GetGameId(str) { 
  const num = str.replace(/[^0-9]/g, ''); 
  return parseInt(num, 10); 
}

export function getMonth (month) {
	let mon = '';
	switch (month) {
		case 2: mon = 'February'; break;
		case 3: mon = 'March'; break;
		case 4: mon = 'April'; break;
		case 5: mon = 'May'; break;
		case 6: mon = 'June'; break;
		case 7: mon = 'July'; break;
		case 8: mon = 'August'; break;
		case 9: mon = 'September'; break;
		case 10: mon = 'October'; break;
		case 11: mon = 'November'; break;
		case 12: mon = 'December'; break;
		default: mon = 'January'; 
	}
	return mon;
}

export function selializing(arr) {
	if (arr && arr.length) {
		return arr.join(', ');
	}
	return ''
}

export function roundNumber(num, decimalPlaces = 0) {
  const p = Math.pow(10, decimalPlaces);
  const n = num * p * (1 + Number.EPSILON);
  return Math.round(n) / p;
}

